import React, {Fragment} from "react";
import { NavLink } from "react-router-dom";
import "./NavigationItems.css";

const NavigationItems = props => {

  const openSubmenu = e => {
    e.preventDefault();
    e.currentTarget.classList.toggle('-active');
  };

  return (
    //   {props.isAuth ? (
    //     <Fragment>
    //       <li className="NavigationItem">
    //         <NavLink exact to="/statistics">
    //           My Statistics
    //         </NavLink>
    //       </li>
    //       <li className="NavigationItem">
    //         <NavLink exact to="/logout">
    //           Logged out
    //         </NavLink>
    //       </li>
    //     </Fragment>
    //   ) : null
    //   // <li className="NavigationItem">
    //   //   <NavLink exact to="/auth">
    //   //     Authenticate
    //   //   </NavLink>
    //   // </li>
    //   }
    // </ul>
    <ul className='NavigationItems'>
      <li className='NavigationItem'>
        <NavLink exact to="/texts" data-color='green'>Texts</NavLink>
      </li>
      <li className='NavigationItem'>
        <NavLink exact to="/news" data-color='blue'>News</NavLink>
      </li>
      <li className='NavigationItem has-submenu' onClick={openSubmenu}>
        <NavLink exact to="/vocabulary" data-color='gold'>Vocabulary</NavLink>
        <ul className='submenu'>
          <li className='NavigationItem'>
            <NavLink exact to="/synonyms">Synonyms</NavLink>
          </li>
          <li className='NavigationItem' >
            <NavLink exact to="/opposite-words">Opposite Words</NavLink>
          </li>
          <li className='NavigationItem' >
            <NavLink exact to="/words-constructor">Words Constructor</NavLink>
          </li>
        </ul>
      </li>
      <li className='NavigationItem'>
      <NavLink exact to="/idioms" data-color='red'>Idioms</NavLink>
      </li>
    </ul>
  );
};

export default NavigationItems;



