import React, { Component, Suspense, lazy } from "react";
import {
  HashRouter,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

import Layout from "./HOC/Layout/Layout";
import WordsConstructor from "./containers/WordsConstructor/WordsConstructor";

import Auth from "./containers/Auth/Auth";
import Logout from "./containers/Auth/Logout/Logout";
import Spinner from './components/UI/Spinner/Spinner';
const AsyncOppositeWords = lazy(() => import('./containers/OppositeWords/OppositeWords'));
const AsyncIdioms = lazy(() => import('./containers/Idioms/Idioms'));

class App extends Component {
  render() {
    let routes = null;
    let isAuthenticated = localStorage.getItem("token");
    if (isAuthenticated) {
      // routes = <Switch />;
    } else {
      routes = (
        <Switch>
          <Route path="/auth" component={Auth} />
          <Route path="/logout" component={Logout} />
          <Route exact path="/opposite-words" render={() => <AsyncOppositeWords page='5'/>} key='1'/>//keyed to pervent caching
          <Route exact path="/synonyms" render={() => <AsyncOppositeWords page='6'/>} key='2'/>
          <Route exact path='/idioms' render={() => <AsyncIdioms />} />
          <Route path="/" component={WordsConstructor} />
          <Redirect to="/" />
        </Switch>
      );
    }
    return (
      <HashRouter basename='/app'>
        <Suspense fallback={<div>Loading...</div>}>
          <Layout>{routes}</Layout>
        </Suspense>
      </HashRouter>
    );
  }
}

export default App;



